@import url('https://fonts.googleapis.com/css?family=Overlock:400,400i,700&display=swap&subset=latin-ext');
@import url('https://fonts.googleapis.com/css?family=Inconsolata:400&display=swap&subset=latin-ext');

html {
    font-family: Overlock, sans-serif;
    font-size: 16pt;
    margin: 0;
    padding: 0;
}

pre, code {
    font-family: Inconsolata, monospace;
    padding: 0;
}

body {
    max-width: 900px;
    margin: 1ex auto;
    counter-reset: chapter;
    background-color: white;
    padding: 16px 32px;
    border-radius: 8px;
}

table {
    border-collapse: collapse;
    margin-left: auto;
    margin-right: auto;
}

tr {
    border-width: 1px;
}

th, td {
    padding: 5px;
    border-style: solid;
    border-width: 1px;
    border-color: gray;
}

.triangolare td:empty {
    border-style: none;
}

.triangolare tbody > tr > td:first-child {
    border-style: none;
}

.triangolare > tbody > tr:last-child > td {
    border-style: none;
}

table.sdd {
    margin: 1em auto;
    border-style: solid none;

    & > thead > tr {
	border-style: none none solid none;
    }

    & > * > tr > td, th {
	border-style: none;
    }

    & > * > tr > td:first-child, th:first-child {
	border-style: none solid none none;
    }
}

.parsing {
    margin: 1em auto;
}

.parsing * > tr > *:first-child {
    border-width: 1pt 2pt 1pt 1pt;
}

.parsing * > tr:first-child > th {
    border-width: 1pt 1pt 2pt 1pt;
}

.parsing > *:first-child > tr:first-child > *:first-child {
    border-width: 0 1pt 1pt 0;
}

@page {
    size: A4 !important;
    margin: 4cm !important;
}

